import Vue from 'vue'
import VueNativeSock from 'vue-native-websocket'
import Vuex from 'vuex'
import { RecycleScroller } from 'vue-virtual-scroller'
import JsonTree from 'vue-json-tree'
import VueWorker from 'vue-worker';
import VueMaterial from 'vue-material'

import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css' // This line here
Vue.config.productionTip = false

Vue.use(VueWorker);
Vue.use(Vuex)
Vue.use(VueMaterial)
Vue.component('RecycleScroller', RecycleScroller)

Vue.component('json-tree', JsonTree)


import App from './App.vue'
import store from './Store'
import router from './Route'

var worker;
Vue.use(VueNativeSock, 'ws://localhost:9009/ws', {
  reconnection: true, // (Boolean) whether to reconnect automatically (false)
  reconnectionDelay: 100, // (Number) how long to initially wait before attempting a new (1000)
  store: store,
  passToStoreHandler: function (eventName, event, next) {
    if (eventName == 'SOCKET_onmessage') {
      const data = event.data

      //worker.postMessage([event]);
      worker.postMessage('ws', [data])
        .then(it => store.commit('newData', it))
    } else {
      next(eventName, event)
    }
  }
})

function onWsEvent(event) {    
    const chunks = event.split("\n")
    const messages = chunks.map(it => JSON.parse(it))

    const calls = messages.filter(it => !it.Log).map(it => Object.assign(it, { id: it.ID}))
    const logs = messages.filter(it => !!it.Log).map(it => {
      const stack = it.Log.Stack
      let entries = []      
      for (let i = 1; i < stack.length; i+=2) {
        entries.push({
          func: stack[i],
          file: stack[i + 1]
        })
      }

      return Object.assign(it.Log, { id: it.Log.ID, entries })
    })

    return {calls, logs}
    //next(eventName, splitted)
}

new Vue({
  router,
  store,
  render: h => h(App),
  created() {
    worker = this.$worker.create([
      { message: 'ws', func: onWsEvent},
    ])
  },
  methods: {

  }
})
.$mount('#app')
